import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './modules/general/home/home.component';
import { NotFoundComponent } from './modules/general/not-found/not-found.component';
import { LandingComponent } from './modules/general/landing/landing.component';
import { DevelopedWithComponent } from './modules/general/bw/bw.component';

const routes: Routes = [
  {
    path: '',
    component: LandingComponent,
  },
  { path: 'email-verifier', component: HomeComponent, pathMatch: 'full' },
  {
    path: 'developed-with',
    component: DevelopedWithComponent,
    pathMatch: 'full',
  },
  { path: '*', component: NotFoundComponent },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      // initialNavigation: 'enabledBlocking',
      useHash: false,
      anchorScrolling: 'enabled',
      scrollPositionRestoration: 'enabled',
    }),
  ],
  exports: [RouterModule],
  declarations: [],
})
export class AppRoutingModule {}
